<template>
  <div>
    <h3>用户主页控制面板</h3>
    <div id="conbox" class="control_box">
      <div v-for="(item,i) in navinit" :key="i">
        <div @click="navclick(i)" :class="{'noselect':navs[i] == 0}">{{item}}</div>
      </div>
    </div>
    <div class="outBtn">
      <div @click="exit">退出登录</div>
    </div>
    <!-- 开启导航弹出框 -->
    <el-dialog
      :class="{'upnone':filelist.length >= 1}"
      :title="`修改“${navinit[index]}”信息`"
      :visible.sync="opennavflage"
      id="loginDlog"
    >
      <el-form :model="navitemdata" :rules="rules" class="elForm" ref="updialog">
        <el-form-item prop="title">
          <el-input v-model="navitemdata.title" placeholder="主标题" prefix-icon="el-icon-sunny"></el-input>
        </el-form-item>
        <el-form-item prop="ctitle1">
          <el-input v-model="navitemdata.ctitle1" placeholder="子标题1" prefix-icon="el-icon-sunrise"></el-input>
        </el-form-item>
        <el-form-item prop="ctitle2">
          <el-input
            v-model="navitemdata.ctitle2"
            placeholder="子标题2"
            prefix-icon="el-icon-sunrise-1"
          ></el-input>
        </el-form-item>
      </el-form>
      <!-- 文件上传区域 -->
      <el-upload
        ref="upload"
        :action="$http.defaults.baseURL+'/uplod'"
        name="img"
        list-type="picture-card"
        :limit="1"
        :on-success="upsucceed"
        :on-exceed="upexceed"
        :on-progress="upprogress"
        :on-error="uperror"
        :on-remove="imgremove"
        :file-list="filelist"
      >
        <i class="el-icon-plus"></i>
      </el-upload>
      <i class="imgtitle">只能上传一张图片哟~</i>
      <div class="outBtn">
        <div
          @click="isopennav(navs[index])"
          :style="navs[index] == '0' ? 'background-color:skyblue':'background-color:red'"
        >{{navs[index] == '0' ? '开启此项访问':'关闭此项访问'}}</div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="opennavflage = false">取 消</el-button>
        <el-button type="primary" @click="baocun">保存</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      navs: [],
      // 打开导航菜单
      opennavflage: false,
      //   初始化数据
      navinit: ["简介", "博客", "剪影", "伴侣", "旅行", "留言"],
      index: 0,
      //   导航数据
      navitemdata: {
        title: "",
        ctitle1: "",
        ctitle2: "",
        img_url: ""
      },
      filelist: [],
      //   校验规则
      rules: {
        title: [
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" }
        ],
        ctitle1: [
          { min: 3, max: 9, message: "长度在 3 到 9 个字符", trigger: "blur" }
        ],
        ctitle2: [
          { min: 3, max: 9, message: "长度在 3 到 9 个字符", trigger: "blur" }
        ]
      },
      //   回显数据
      showData: []
    };
  },
  methods: {
    isopennav(val) {
      let value = val == 1 ? 0 : 1;
      this.navs[this.index] = value;
      this.navs = this.navs.join(".").split(".");
      //   保存上传数据
      this.baocun();
    },
    baocun() {
      this.$refs["updialog"].validate(async valid => {
        if (valid) {
          let obj = {};
          obj.navs = this.navs.join(",");
          obj[`n${this.index + 1}_img`] = this.navitemdata.img_url;
          obj[
            `n${this.index + 1}`
          ] = `${this.navitemdata.title},${this.navitemdata.ctitle1},${this.navitemdata.ctitle2}`;
          let { data: res } = await this.$http.post("/navs", obj);
          if (res.ok == 0) {
            this.getnavs();
            return this.$notify.error({
              title: "更改失败~",
              customClass: "alter"
            });
          }
          this.$notify.success({
            title: "更改成功~",
            customClass: "alter"
          });
          this.opennavflage = false;
        //   清空表单
          this.$refs['updialog'].resetFields();
          this.navitemdata.img_url = null
        }else{
            this.getnavs();
        }
      });
    },
    //   获取navs
    async getnavs() {
      let { data: res } = await this.$http.get("/navs");
      this.showData = res.data;
      this.navs = res.data.navs.split(",");
    },
    // 退出登录
    exit() {
      sessionStorage.clear();
      // 刷新页面
      location.reload();
    },
    // 点击盒子
    navclick(index) {
      this.index = index;
      let arr = this.showData[`n${index + 1}`].split(",");
      this.navitemdata.title = arr[0];
      this.navitemdata.ctitle1 = arr[1];
      this.navitemdata.ctitle2 = arr[2];
      this.opennavflage = true;
    },
    // 文件上传钩子组
    upsucceed(res, file, filelist) {
      this.filelist = filelist;
      this.$emit("loading", false);
      // 判断是否上传成功
      if (res.ok == 0) {
        setTimeout(() => {
          this.filelist.pop();
        }, 1000);
        this.$refs.upload.clearFiles();
        return this.$notify.error({
          title: "图片不能超过4M哟",
          customClass: "alter"
        });
      }

      // 成功提示
      this.$notify.success({
        title: "上传成功~",
        customClass: "alter"
      });
      //   保存图片路径
      this.navitemdata.img_url = res.src;
    },
    upexceed() {
      this.$notify.error({
        title: "只能上传一张图片",
        customClass: "alter"
      });
    },
    upprogress() {
      this.$emit("loading", true);
    },
    uperror() {
      this.$emit("loading", false);
      this.$notify.error({
        title: "不明原因换个格式试一试",
        customClass: "alter"
      });
    },
    imgremove() {
      this.filelist.pop();
      //   保存图片路径
      this.navitemdata.img_url = null;
    }
  },
  created() {
    // 判断有没有token
    if (!sessionStorage.getItem("token")) {
      return this.$router.push("/");
    }
    // 获取navs
    this.getnavs();
    // this.$emit('loading',true)
  }
};
</script>

<style>
#conbox {
  display: flex;
  /* 自动换行 */
  flex-wrap: wrap;
}
#conbox > div {
  flex: 1;
  width: 16%;
  min-width: 180px;
  max-width: 320px;
  height: 200px;
  margin: 0 auto;
  padding: 20px;
}
#conbox > div > div {
  width: 100%;
  height: 100%;
  background-color: skyblue;
  text-align: center;
  font-size: 26px;
  line-height: 160px;
  background: linear-gradient(to right, #feac5e, #c779d0, #4bc0c8);
  cursor: pointer;
}

#conbox > div > div.noselect {
  background: #f3f3f3;
  opacity: 0.8;
  color: gray;
  /* animation: gray 1s linear; */
  position: relative;
}
#conbox > div > div.noselect::before {
  content: "关闭";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%); /*2d移动*/
  width: 145%;
  height: 145%;
  background-color: black;
  opacity: 0.5;
  color: white;
  font-size: 30px;
  animation: zhezao 1s;
  border-radius: 50%;
  line-height: 232px;
}
.outBtn {
  width: 100%;
  padding: 20px;
}
.outBtn > div {
  width: 100%;
  text-align: center;
  background-color: red;
  height: 40px;
  line-height: 40px;
  margin: 0 auto;
  max-width: 500px;
  cursor: pointer;
}
@keyframes zhezao {
  0% {
    transform: translate(-50%, -50%) scale(0);
  }
  100% {
    transform: translate(-50%, -50%) scale(1);
  }
}
.imgtitle {
  color: #feac5e;
  font-size: 12px;
  position: relative;
  top: 10px;
}
.upnone .el-upload.el-upload--picture-card {
  display: none !important;
}
.elForm div {
  overflow: visible;
}
</style>